class WatercolorPage extends React.Component { //the whole page. there will be multiple layouts which is why this is not done with APP directly
  constructor(props) {
    super(props);
    this.state = {scrolled: false,
                    content: {"sections": []},
                    isLoaded: false,
                    imagesLoaded: false
                };
  }

  waitForLoad(el) {
    if (el) {
      el.addEventListener('load', this.imageLoaded);
      ++this.imgCount;
    }
  }
  imageLoaded({target}) {
    target.removeEventListener('load', this.imageLoaded);

    if (++this.imgLoadCount === this.imgCount) {
        this.setState({isLoaded: true})
    }
  }

  componentDidMount() {
      this.imgCount = this.imgLoadCount = 0;
      fetch("/index.json") //stolen code begins
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            content: result
          });
          setTimeout(() => { // TODO: make this actually check on images
              this.setState({
                imagesLoaded: true,
              });
          }, 2000)
        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
    ) //stolen code ends

    const script = document.createElement("script");
    script.src = "/scripts.js";
    script.async = true;
    document.body.appendChild(script);
    //new AnimatedText(document.getElementById("babyy"),["Industrial","Regenerative","Systems"], 4000, 10);

    if (this.state.scrolled === false && window.scrollY >= 50) {
        this.setState({ scrolled: true });
    }
    document.addEventListener('scroll', () => {
        //alert(window.scrollY);
        if (this.state.scrolled === false && window.scrollY >= 50) {
            this.setState({ scrolled: true });
        } else if (this.state.scrolled === true && window.scrollY < 50) {
            this.setState({ scrolled: false });
        }
    });
  }

  render() {
    return (
      <div className="watercolor-page">
          <WatercolorPens />
          <div className={"watercolor-splash".concat(this.state.isLoaded && this.state.imagesLoaded ? "  isloaded" : "")}></div>
          <WatercolorLeader />
          {this.state.content.sections.map((section, index) => (
              <WatercolorSection section={section} />
          ))}
          <WatercolorFollower />
      </div>
    );
  }
}

function WatercolorLeader(props) { //first segment
  return (
    <div className="watercolor-leader">
        <div className="watercolor-category"></div>
        <div className="watercolor-beginning">
            <h1><span id="babyy">Industrial</span> Design is My Passion</h1>
            <p>These are things I made! I care about sustainability and community and stuff. I'm also real good at CAD and sewing.</p>
            <div className="watercolor-sheet-tip"></div>
        </div>
        <div className="watercolor-introduction">
        </div>
    </div>
  );
}

class WatercolorSection extends React.Component { //container for projects of same category
    constructor(props) {
        super(props);
        this.state = {stuck: false};
        this.stickyScroll = this.stickyScroll.bind(this);
        this.categoryRef = React.createRef();
        //document.addEventListener('scroll', this.stickyScroll)
    }
    stickyScroll(){
        console.log(window.scrollY);
        console.log(this.categoryRef.current.offsetTop);
        if (window.scrollY >= this.categoryRef.current.offsetTop){
            console.log(this.state.stuck);
            this.setState({ stuck: true });
        }
    }
    render() {
        return (
        <div className="watercolor-section">
            <div className="watercolor-category"><div ref={this.categoryRef} onClick={this.stickyScroll} className={(this.state.stuck ? "stuck" : "")}><h2>{this.props.section.title}</h2></div></div>
            <div className="watercolor-slides">
                {this.props.section.slides.map((slide, index) => (
                    <WatercolorSlide slide={slide} />
                ))}
            </div>
        </div>
      );
    }
}

function WatercolorSlide(props) { //individual projects
  return (
    <div className="watercolor-slide">
        <div className="watercolor-sheet">
            <Link to={props.slide.url}>
                <img className="watercolor-image" src={props.slide.image} alt={"watercolor thumbnail for " + props.slide.title} />
            </Link>
        </div>
        <div className="watercolor-sidebar">
            <div>

                <h1><Link to={props.slide.url}>{props.slide.title}</Link></h1>

                <span>{props.slide.subtitle}</span><br/>
                <span>{props.slide.topics}</span>
                <p>{props.slide.description}</p>
            </div>
        </div>
    </div>
  );
}

function WatercolorFollower(props) { //first segment
  return (
    <div className="watercolor-follower">
        <div className="watercolor-category"></div>
        <div className="watercolor-ending">
            <div className="watercolor-sheet-tip"></div>
            <h1>Thats it! ...or is it?</h1>
            <p>Hope you enjoyed! Now go back and beat it on hard mode</p>
            <br/>
            <a className="watercolor-follower-link" href="/about" target="_blank">About</a>
            <br/>
            <span>© Rose Kirby | 2021</span><br/><br/>
            <a className="watercolor-follower-link" href="https://instagram.com" target="_blank" rel="noreferrer">instant gram</a>
            <a className="watercolor-follower-link" href="/behance.txt" target="_blank">bee hands</a>
        </div>
        <div className="watercolor-conclusion">
        <h2>Contact me!</h2><br/>
        <a href="mailto:rose@rosekir.by" target="_blank" rel="noreferrer">There's more where that came from</a>
        </div>
    </div>
  );
}

function WatercolorPens() { //individual projects
  return (
    <img className="watercolor-pens" src={"/images/pens.png"} alt="" />
  );
}



// BREAK


class ProjectPage extends React.Component { //whole page containing a project
    constructor(props) {
        super(props);
        this.state = {scrolled: false,
                        content: {"children": []},
                        isLoaded: false,
                        imagesLoaded: false
                    };
    }

    componentDidMount() {
        this.imgCount = this.imgLoadCount = 0;
        fetch("/projects.json") //stolen code begins
        .then(res => res.json())
        .then(
          (result) => {
                console.log(this.props.slug);
                this.setState({
                  isLoaded: true,
                  content: result.projects.find(p => p.slug == this.props.slug)
              });
                setTimeout(() => { // TODO: make this actually check on images
                    this.setState({
                      imagesLoaded: true,
                    });
                }, 2000)
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            console.log(error);
            this.setState({
              isLoaded: true,
              error
            });
          }
      ) //stolen code ends
    }

    render() {
        if (!this.state.content){
            console.log("page has no data");
            return(
                <Redirect to="/" />
            );
        }
        return(
            <div className="project-page">
                {this.state.content.children.map((child, index) => (
                    componentLookup(child.component, child.props)
                ))}
            </div>
        );
    }
}

function componentLookup(name, props = {}) {
  switch(name) {
    case 'ProjectFullImage': return <ProjectFullImage props={props} />;
    case 'ProjectTitle': return <ProjectTitle props={props} />;
    case 'ProjectHeading': return <ProjectHeading props={props} />;
    case 'ProjectImageByText': return <ProjectImageByText props={props} />;
    case 'ProjectImageByImage': return <ProjectImageByImage props={props} />;
    case 'ProjectColumns': return <ProjectColumns props={props} />;
    case 'ProjectCycleThree': return <ProjectCycleThree props={props} />;
    case 'ProjectImageCaptioned': return <ProjectImageCaptioned props={props} />;
    case 'ProjectImageGallery': return <ProjectImageGallery props={props} />;
  }
}

function parseMarkup(str) {
    var boldArray = str.split("**");
    var boldResult = ""
    var boldThis = ((boldArray[0] === "") ? true : false)
    for (var i = 0; i < boldArray.length; i++) {
        if (boldThis) {
            console.log("bolded " + boldArray[i]);
            boldResult = boldResult.concat("<strong>" + boldArray[i] + "</strong>");
        } else {
            console.log("didn't bold " + boldArray[i]);
            boldResult = boldResult.concat(boldArray[i]);
        }
        boldThis = !boldThis;
    }
    return boldResult;
}
